import React from "react";
import photo from "../../img/Image_edited.jpg";

function Context() {
  return (
    <div className="row contentArea d-flex align-items-center my-5">
      <div className="col-12 col-sm-6 photoArea">
        <img
          src={photo}
          alt=""
          srcset=""
          className="photo w-100 p-5 rounded-pill"
        />
      </div>
      <div className="col-12 col-sm-6 text">
        <h4 className="mt-5 mb-3 name text-center">Merhaba Ben Dr. Murat Gkduman</h4>
        <p className="ben text-center">
           yeri hekimi, tam yn web gelitiricisi ve oyun gelitiricisi olarak eitli alanlarda kendimi gelitiriyorum.
        </p>
        <p className="ben text-center">
          FullStack web gelitirme becerilerimle web uygulamalar gelitirmek iin alyorum. Ayrca, Unity ile oyun gelitirmek benim iin bir tutku haline geldi. Oyun dnyasnda yaratc fikirlerimi hayata geirirken, amatr fotoraf kimliimle de sanatn gcnden ilham alyorum.
        </p>
        <p className="ben text-center">
          En byk tutkum, srekli olarak yeni eyler renmek ve kendimi gelitirmektir. Kitaplar, videolar ve uygulamal deneyimler araclyla srekli renme srecimde ilerlerken, bu bilgi birikimini ilerime ve projelerime yanstmak benim iin heyecan verici bir deneyimdir.
        </p>
      </div>
    </div>
  );
}

export default Context;
